
























































import { Component, Vue, Watch } from 'vue-property-decorator'
import axios, { AxiosPromise } from "axios"

@Component
export default class Index extends Vue {
  private userId: string = ""
  private userEmail: string = ""
  private userPwd: string = ""

  // ------------------------------------------------------------
  // lifecyle hook
  mounted () {
    // this.$router.push('intro')

  }

  /**
   * 회원가입을 수행한다.
   */
  private async userSignInfoSave () {
    const data = {
      userId: this.userId,
      userEmail: this.userEmail,
      userPwd: this.userPwd
      // password: btoa(this.userPwd)

    }

    const config = {
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      }
    }

    /* Id를 입력하지 않았을 경우 */
    if (this.userId === '') {
      Vue.$alert('Please check the id')
      return
    }

    /* Email를 입력하지 않았을 경우 */
    if (this.userEmail === '') {
      Vue.$alert('Please check the Email')
      return
    }

    /* Password를 입력하지 않았을 경우 */
    if (this.userPwd === '') {
      Vue.$alert('Please check the password')
    }

    /* Login 검증 */
    await this.$bizApi.commBiz.userSignUp(data, config)
      .then((res) => {
        Vue.$alert(res.data.msg)
        if (res.data.login === true) {
          this.$router.replace('index')
        } else {
          this.$log.error('Please to check data from Server')
        }
      }).catch((err) => {
        this.$log.error(err)
      })
  }
}
